/* General Styles */
body {
    margin: 0;
    padding: 0;
    font-family: "Inter", sans-serif;
      font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  font-variation-settings:
    "slnt" 0;
    background-color: hsl(210, 80%, 50%);
    background: rgb(174,200,238);
background: linear-gradient(90deg, rgba(174,200,238,1) 0%, rgba(25,113,216,1) 100%);
    color: rgb(0, 0, 0);
    
}

.inter-fontinter {
  font-family: "Inter", sans-serif;
  font-optical-sizing: auto;
  font-weight: 750;
  font-style: normal;
  font-variation-settings:
    "slnt" 0;
}
.container {
    margin: 0 auto;
}

.header {
    padding: 20px 0;
    text-align: center;
    color:#000000
}

.header h1 {
    margin: 0;
    font-size: 2.5rem;
}

.main-content {
    padding: 40px 0;
}

.section {
    margin-bottom: 40px;
    max-width: 1200px;
        padding: 20px;
}

.section h2 {
    color: #ffffff; /* Yellow */
}

.section p {
    line-height: 1.5;
}

.footer {
    color: white;
    text-align: center;
    padding: 20px 0;
}
